.button {
	color: #000;
	line-height: 17 / 14 * 100%;
	font-size: 14px;
	font-weight: 700;
	background-color: #fff;
	border-radius: 10px;
}
.btnIn {
	padding: 10px 101px;
}
.btnUp {
	padding: 10px 46px;
}

.lock {
	position: absolute;
	transform: translate(-150%, -10%);
}
